.ts-track {
    width: fit-content;
    display: flex;
    align-items: center;
    margin: 0;
    z-index: 4000;
}

.ts-clear-track {
    justify-content: center;
}

.ts-nm-track {
    justify-content: flex-start;
}

.ts-track-logo {
    background-color: #222222aa;
    backdrop-filter: blur(2px);
    height: 90px;
    width: 90px;
    padding: 10px;
    cursor: pointer;
}

.ts-track-logo .exercism-badges {
    width: 100%;
    height: 100%;
    stroke: black;
    fill: none;
}

.ts-track:hover .track-clear {
    text-shadow: 0 0 5px white;
}

.track-clear {
    font-family: cabin;
    color: white;
    z-index: 55;
}

.ts-track .track-clear {
    position: absolute;
}

.ts-track .ts-title-track {
    position: absolute;
}

.ts-track-title {
    background-color: transparent;
    max-width: 0;
    color: transparent;
    margin: 5px;
    padding: 10px;
    background-color: transparent;
    border-radius: 12px;
    transition: all 0.3s ease;
    font-family: altona;
    font-size: 1.3rem;
    font-weight: 800;
    z-index: 1000;
}

.ts-track:hover .ts-track-title {
    max-width: 1000px;
    color: black;
    background-color: white;
}

.ts-tes-count {
    font-family: nunito;
    font-size: 1.1rem;
    font-weight: 400;
    transition: all 0.3s ease;
}

@media screen and (max-width: 720px) {
    .ts-track-logo {
        height: 100%;
        margin: 0;
        padding: 0;
    }
    .ts-track {
        position: relative;
        flex-direction: column;
    }
    .ts-track-title {
        display: none;
    }
    .track-clear {
        font-size: 0.6rem;
    }
    .ts-track-logo {
        padding: 5px;
    }
}